@import url("https://fonts.googleapis.com/css2?family=Poppins&display=swap");
* {
  margin: 0%;
  padding: 0%;
  box-sizing: border-box;
  font-family: "Poppins", sans-serif;
}

.container {
  max-width: 1600px;
  padding: 0 15px;
  margin: 0 auto;
}

.alignment {
  display: flex;
  justify-content: center;
}

/* Navbar  start */
.header-inner.header-left {
  display: flex;
}

.header-inner-main {
  display: flex;
  padding: 15px 0;
  align-items: center;
}

section.header-main {
  position: fixed;
  z-index: 3;
  background-color: white;
  top: 0;
  width: 100%;
}

.header-inner.header-left {
  width: 15%;
}

.header-inner.header-center {
  width: 45%;
}
.header-inner.header-right {
  width: 40%;
}
.navbar-list {
  display: flex;
  gap: 5%;
  color: #dcdcdf;
  list-style: none;
}
img.cart-btn {
  height: 26px;
  width: 26px;
}
.cart-icon {
  height: 26px;
  width: 26px;
}
.main-demo {
  position: relative;
}
.nav__menu--right {
  position: absolute;
  right: -300px;
  width: 300px;
  height: 100%;
  background-color: #444;
  z-index: 1;
  transition: 400ms ease;
}

.nav__menu--right.open {
  height: 100%;
  transform: translateX(-300px);
}

.nava {
  text-decoration: none;
  color: #000000;
}

.navbar .nava:hover {
  color: #2f3031a2;
  cursor: pointer;
}

.navbar:hover {
  color: #2f3031a2;
  cursor: pointer;
}

.navbar {
  transition: 0.6s ease;
}

p.user_detail {
  font-size: 18px;
  font-weight: 500;
}

.header-inner.header-right {
  gap: 15px;
  align-items: center;
  justify-content: end;
  display: flex;
}

button.btn-getstarted {
  padding: 10px 25px;
  font-size: 15px;
  color: white;
  text-align: center;
  border: #000000;
  border-radius: 8px;
  background-color: #000000;
  transition: 0.6s ease;
  border: 1px solid #000000;
  cursor: pointer;
}

button.btn-getstarted:hover {
  background: #2f3031a2;
  color: #fff;
  cursor: pointer;
  border: 1px solid #fff;
}

section.header-main.darkHeader {
  position: fixed;
  background: #fff;
  transition: 0.6s ease;
}

section.header-main.darkHeader .navbar-list .nava {
  color: #040406;
}

section.header-main.darkHeader .navbar-list .nava:hover {
  color: #0000e6;
}

section.header-main.darkHeader button.btn-getstarted:hover {
  background: #43276f;
  cursor: pointer;
  border: 1px solid #43276f;
}

img.logo.hide {
  display: none;
}

img.logo-2.hide-2 {
  display: none;
}
/* Navbar  End */

/* Banner Slider  start */
.swiper {
  width: 100%;
  height: 100%;
  margin-top: 42px;
}

.swiper-slide img {
  display: block;
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.swiper.swiper-initialized.swiper-horizontal.swiper-pointer-events.swiper-backface-hidden.mySwiper {
  padding: 10px 65px 0px;
  margin-top: 69px;
}

.swiper-pagination-bullet-active {
  padding: 2px 20px;
  background-color: #000000 !important;
}

.swiper-pagination-bullet {
  border-radius: 10px !important;
}
.swiper-horizontal > .swiper-pagination-bullets,
.swiper-pagination-bullets.swiper-pagination-horizontal,
.swiper-pagination-custom,
.swiper-pagination-fraction {
  bottom: 24px !important;
}
/*Banner Slider  end */
/*Discount Slider start */
.discount-card {
  max-width: 33%;
  width: 32%;
  margin: 20px 10px;
  border-radius: 10px;
}

.swiper-button-prev,
.swiper-button-next .swiper-rtl .swiper-button-next {
  left: 10px;
  right: auto;
  background: #2f3031a2;
  border-radius: 50px;
  padding: 22px;
}

.swiper-button-next,
.swiper-rtl .swiper-button-prev {
  right: 10px;
  left: auto;
  background: #2f3031a2;
  border-radius: 50px;
  padding: 22px;
}
.swiper-button-next:after,
.swiper-button-prev:after {
  font-family: swiper-icons;
  font-size: 20px !important;
  text-transform: none !important;
  letter-spacing: 0;
  text-align: center;
  color: white;
  font-feature-settings: inherit;
  font-variant: initial;
  line-height: 1;
}
/*Discount Slider end */

/*Product list start */
.product-list {
  display: flex;
  flex-wrap: wrap;
}
img.product-img {
  width: 100%;
  border-radius: 6px 6px 6px 6px;
}
.product-card {
  max-width: 19%;
  padding: 20px 10px;
  margin: 0px 5px;
  margin-bottom: 10px;
}

.product-card:hover {
  padding: 6px 10px;
  cursor: pointer;
  transition: 0.5s;
  border-radius: 10px;
}

h5.product-name {
  padding-bottom: 5px;
  -webkit-line-clamp: 1;
  display: -webkit-box;
  -webkit-box-orient: vertical;
  text-overflow: ellipsis;
  overflow: hidden;
  font-size: 16px;
}
h5.product-details {
  font-size: 14px;
  display: -webkit-box;
  -webkit-line-clamp: 1;
  -webkit-box-orient: vertical;
  text-overflow: ellipsis;
  overflow: hidden;
  padding-bottom: 5px;
  font-weight: 500;
}
h1.cat-name {
  margin-top: 25px;
  font-size: 30px;
  font-weight: 600;
  margin-bottom: 5px;
}
/*Product list end */
/*Off banner start */
.off-card {
  border-radius: 10px;
  max-width: 100%;
  width: 100%;
}
.off-card:hover {
  cursor: pointer;
}
/*Off banner end  */

/*Services Section start */
.wrap-service-card {
  display: flex;
  border-radius: 5px;
  justify-content: space-between;
}
.service-card {
  padding: 30px;
  display: grid;
  max-width: 25%;
  width: 25%;
  border: 0.1px solid wheat;
  justify-content: center;
  align-items: center;
  justify-items: center;
}
.service-img {
  height: 100px;
  width: 100px;
  padding: 10px;
}

.service-tittle {
  text-align: center;
  font-size: 18px;
}
.service-subtittle {
  padding: 10px;
  font-weight: 400;
  text-align: center;
}
.wrap-service-card {
  background-color: #f9f9f9;
  margin-top: 20px;
  margin-bottom: 20px;
}

/*Services Section end */

/*Collection Section Start */
.collection-container {
  margin-top: 69px;
}
.collection-list {
  max-width: 20%;
  overflow: scroll;
  height: 100vh;
  width: 20%;
}
.collection-list::-webkit-scrollbar {
  display: none;
}
.collection-data {
  max-width: 80%;
  height: 100vh;
  overflow: scroll;
  width: 80%;
}
.collection-data::-webkit-scrollbar {
  display: none;
}
.collection-container {
  display: flex;
}
.collection-name {
  margin-top: 25px;
  font-size: 24px;
  font-weight: 600;
}
.collection-line {
  width: 80%;
  margin-top: 35px;
  size: 1;
}
.collectionlist {
  padding: 10px 0px;
  font-size: 15px;
  font-weight: 500;
}
.collectionlist:hover {
  font-weight: 800;
  cursor: pointer;
}

.listwrap {
  padding: 20px 0px;
}

button.btn-loadmore {
  padding: 10px 25px;
  font-size: 15px;
  color: white;
  text-align: center;
  border: #000000;
  border-radius: 8px;
  background-color: #000000;
  transition: 0.6s ease;
  border: 1px solid #000000;
  cursor: pointer;
}

button.btn-loadmore:hover {
  background: #2f3031a2;
  color: #fff;
  cursor: pointer;
  border: 1px solid #fff;
}
.btn-loadmore {
  margin-top: 10px;
  margin-bottom: 10px;
  display: flex;
  align-content: center;
  justify-content: center;
}
/*Collection Section End */

/* Cart Page */
.cart-wrap {
  padding: 10% 5%;
  height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
.cart-head {
  display: flex;
  justify-content: space-between;
}

.card-mid-wrap {
  display: flex;
  flex-direction: column;
  align-content: center;
  justify-content: center;
  align-items: center;
}
.cart-emp-tittle {
  padding-top: 3%;
  font-size: 20px;
}
img.empcart {
  width: 40%;
  align-content: center;
}

.checkout-btn {
  gap: 10%;
  display: flex;
}
.checkout-btn {
  gap: 10%;
  border-radius: 10px;
  padding: 12px;
  background: #999999;
  display: flex;
  justify-content: space-around;
}
.checkout-btn-tittle,
.checkout-total {
  color: white;
  font-weight: 400;
}

/*Cart page end */

/*ProductDetails page Start */
.details-wrap {
  margin-top: 70px;
  display: flex;
  gap: 2%;
}
.productdetails-img {
  max-width: 50%;
  width: 50%;
}
.product-details {
  max-width: 50%;
  width: 50%;
}
.prroductdetails-name {
  font-size: 28px;
}
.prroductdetails-details {
  font-weight: 400;
  margin-top: 1%;
  font-size: 15px;
}
.productdetails-price {
  margin-top: 1%;
  margin-bottom: 1%;
  font-size: 32px;
}
.productdetails-size,
.productdetails-colors {
  margin-top: 2%;
  margin-bottom: 1%;
  font-size: 18px;
  font-weight: 550;
}
.size-list {
  display: flex;
  padding-bottom: 2%;
  gap: 2%;
}
.size {
  padding: 6px;
  text-align: center;
  border: solid 1px transparent;
  width: 6%;
}

.size:hover {
  cursor: pointer;
  border: solid 1px;
  border-radius: 5px;
}
.productdetails-colorswrap {
  display: flex;
  gap: 2%;
}

.qty-wrap {
  display: flex;
  font-weight: 500;
  margin: 20px 0px;
  align-items: center;
}
.qty-total {
  font-size: 25px;
  border-top: solid 1px;
  border-bottom: solid 1px;
  padding: 0px 35px;
  font-weight: 500;
}
.qty-btn {
  font-size: 25px;
  border: solid 1px;
  padding: 0px 10px;
  font-weight: 500;
}
.qty-btn.pluse {
  border-top-right-radius: 5px;
  border-bottom-right-radius: 5px;
}
.qty-btn:hover {
  cursor: pointer;
  border: solid 1px black;
  background-color: black;
  color: white;
  transition: 0.6s;
}
.qty-btn.minus {
  border-top-left-radius: 5px;
  border-bottom-left-radius: 5px;
}

h3.add-cart-btn-tittle {
  font-size: 12px;
  font-weight: 400;
}
.productdetails-addcart-btn {
  margin-left: 15px;
  text-align: center;
  border-radius: 5px;
  color: white;
  width: 30%;
  border: solid 1px #999999;
  padding: 10px;
  background-color: #999999;
}
.productdetails-addcart-btn:hover {
  cursor: pointer;
  color: white;
  background-color: black;
}
span.productdetails-contant {
  font-size: 15px;
  font-weight: normal;
  color: gray;
}

.accordion {
  max-width: 600px;
  margin: 2rem auto;
}

.accordion-title {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  cursor: pointer;
  background-color: white;
}

.accordion-title,
.accordion-content {
  padding: 1rem;
}

.accordion-content {
  background-color: white;
}

/*ProductDetails page End */
/*footor start*/

.footer-wrap {
  margin-top: 3%;
  margin-bottom: 2%;
}

.w-100 {
  width: 100%;
}
.pad15 {
  padding: 15px;
}
.row {
  display: flex;
  justify-content: space-between;
}
a {
  text-decoration: none;
}
.social-icon img {
  width: 40px;
  padding-right: 1rem;
}
.social-icon {
  font-size: 15px;
  color: #5a5a5a;
  display: flex;
  padding: 0.5rem 0;
}
.social-contact p a {
  color: #5a5a5a;
  font-size: 15px;
  padding: 0.5rem 0;
  transition: all 0.5s;
}
.social-contact p a:hover {
  color: #000;
}
.footer-heading {
  font-size: 18px;
  font-weight: 500;
  padding: 0 0 1.5rem 0;
}
.desc {
  padding: 0.5rem 0;
}
/*footor end*/

/* 1440 responsive start */

@media (max-width: 1500px) {
  .discount-card {
    width: 31%;
  }
  .product-card {
    max-width: 25%;
    padding: 20px 10px;
    margin: 0px 0px;
    margin-bottom: 10px;
  }
}
/* 1440 responsive end */
